import React, { useEffect, useRef, useState } from "react";

import { Button, Form, Modal, Spinner } from "react-bootstrap";

// import required modules
import { ArrowLeft, Trash2 } from "react-feather";
import { Card } from "reactstrap";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  editQuestion,
  getQuestionsById,
  getSearchedQuestion,
  getSpecificQuestion,
} from "../../api/questions";
import { ErrorHandler } from "../errorHandler";
import { CircularProgress } from "@mui/material";
import Select, { components } from "react-select";
import { message } from "antd";
import { faSlash } from "@fortawesome/free-solid-svg-icons";

const EditQuestion = () => {
  const countryOptions = [
    { value: 1, label: "Saudi Arabia (SA)" },
    { value: 2, label: "United Arab Emirates (UAE)" },
    { value: 3, label: "Kuwait (KU)" },
    { value: 4, label: "Qatar (QA)" },
    { value: 5, label: "Bahrain (Behr)" },
    { value: 6, label: "Oman" },
  ];
  const categoryOptions = [
    { value: "pharma", label: "Pharma" },
    { value: "nonpharma", label: "Non-Pharma" },
    { value: "structure", label: "Structure" },
    { value: "calculation", label: "Calculation" },
  ];
  const scrollPositionRef = useRef(0);
  const id = useParams();
  const [findQustion, setFindQustion] = useState(null);
  const [page, setPage] = useState(1);
  const [lastId,setLastId]=useState(1)
  const [selectCountryOptions, setselectCountryOptions] = useState(null);
  const [selectCatOptions, setSelectCatOptions] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const navigate = useNavigate();
  const [editLoader, setEditLoader] = useState(false);
  const [title, setTitle] = useState("");
  const [loading, setLoading] = useState(null);
  const [examOptions, setExamOptions] = useState(null);
  const [exams, setExams] = useState([]);

  const [questions_, setQuestions_] = useState([""]);
  const [searchLoading, setsearchLoading] = useState(false);
  const [correctAns, setCorrectAns] = useState("");
  const [questions, setquestions] = useState([]);
  const [questionsId, setQuestionsId] = useState("");
  const addQuestions = () => {
    setQuestions_((prev) => [...prev, ""]);
  };

  const handleQueries = (val, index) => {
    const newArray = [...questions_];
    newArray[index] = val.target.value;
    setQuestions_(newArray);
  };
  const delQuestions = (index) => {
    const filteredData = [...questions_];
    filteredData?.splice(index, 1);
    setQuestions_(filteredData);

  };
  const hanldeEditQuestion = (e) => {
    e.preventDefault();
    setEditLoader(true);

    const data = {
      options: questions_,
      title: title,
      answers: correctAns,
      exam:examOptions?.value
    };
      editQuestion(data,id?.id)
        .then((res) => {
          if (res?.data) {
            message.success("Question Updated successfully ");
            setEditLoader(false);
            navigate('/exam')
            setFindQustion(res?.data?.question);
            setTitle("");
            setQuestions_([""]);
            setSelectCatOptions(null)
            setselectCountryOptions(null)
            setExamOptions(null)
            setQuestionsId("")
            setCorrectAns('');
            setFindQustion(null)
            setExams([])
          }
        })
        .catch((err) => {
          ErrorHandler(err);
          setEditLoader(false);
        });

  };
  const handleCategoryChange = (selectedOption) => {
    setSelectCatOptions(selectedOption);
    setPage(1)
    setExamOptions(null)
  };
  const handleChange = (selectedOption) => {
    setselectCountryOptions(selectedOption);
    setExamOptions(null)

  };
  const handleExamChange=(selectedOption)=>{
    setExamOptions(selectedOption)

  }
  const handlePopupScroll = (e) => {
    const { target } = e;
    scrollPositionRef.current = target.scrollTop;
    if (
      Math.ceil(target.scrollHeight) - Math.ceil(target.scrollTop) ===
      Math.ceil(target.clientHeight)
    ) {
      if (hasMore && !searchLoading) {
        setPage((prevPage) => prevPage + 1);
        
      }
    }
  };
  useEffect(() => {
    setLoading(true)
    getSpecificQuestion(id?.id)
      .then((res) => {
        setFindQustion(res?.data);
    setLoading(false)

      })
      .catch((er) => {
        ErrorHandler(er);
    setLoading(false)

      });
  }, [id]);
  useEffect(()=>{
    setsearchLoading(true)
    getSearchedQuestion(page,selectCountryOptions?.value,(selectCatOptions?.value ||'all' )).then((res) => {
    setsearchLoading(false)

      const newOptions = res?.data?.exams?.map((item) => {
        return { label: item?.title, value: item?._id };
      });
      if(page>=res?.count?.totalPage){
        setsearchLoading(false)
      }
      if (page === 1) {
        setExams(newOptions);
    setsearchLoading(false)

      } else {
        setExams((prevExams) => [
          ...prevExams,
          ...newOptions,
        ]);
    setsearchLoading(false)

      setHasMore(res.count.totalPage > page);
     

      }
          }).catch((err)=>{
            ErrorHandler(err)
    setsearchLoading(false)

          })
  },[selectCatOptions,selectCountryOptions,page])
  useEffect(() => {
    setTitle(findQustion?.question?.title);
    setQuestions_(findQustion?.question?.options);
    setCorrectAns(findQustion?.question?.answers);
    setselectCountryOptions({
      label: findQustion?.exam?.country === 1 ? 'Saudi Arabia (SA)' :
             findQustion?.exam?.country === 2 ? 'United Arab Emirates (UAE)' :
             findQustion?.exam?.country === 3 ? 'Kuwait (KU)' :
             findQustion?.exam?.country === 4 ? 'Qatar (QA)' :
             findQustion?.exam?.country === 5 ? 'Bahrain (Behr)' : 
             findQustion?.exam?.country === 6 ? 'Oman (Om)' : '',
      value: findQustion?.exam?.country
  });
  setExamOptions({label:findQustion?.exam?.title, value:findQustion?.exam?._id})
  setSelectCatOptions({label:findQustion?.exam?.category, value:findQustion?.exam?.category})
  }, [findQustion]);
  return (
    <main className="min-h-screen lg:container py-4 px-4 ">
      <div className="d-flex gap-4 align-items-start w-full">
        <div className="flex items-center gap-3">
          <button
            type="button"
            onClick={() => {
              navigate("/exam");
            }}
            className="flex items-center justify-center p-2 bg_primary rounded-3"
          >
            <ArrowLeft className="text_white" />
          </button>
        </div>
        <div className="flex flex-col mb-3 w-full">
          <h2 className="plusJakara_semibold text_black">Edit Question</h2>
          <h6 className="text_secondary plusJakara_regular">
            Information about your current plan and usages
          </h6>
        </div>
      </div>
      {/* <Swiper
      rewind={true}
      className="mySwiper"
      slidesPerView={8}
      spaceBetween={10}
      navigation={true}
    >
      {arr?.map((item, index) => (
        <>
        <div className={active===index ?'swiper_btn swiper_btn active  d-flex justify-center' : 'swiper_btn  d-flex justify-center ' }>
        <SwiperSlide onClick={()=>handleQuestions(item,index)}  className={active===index ?'swiper_btn active' : 'swiper_btn ' } key={index}>{item+1}</SwiperSlide>
        </div>
        {}
        </>
      ))}
    </Swiper> */}
    {
      loading ? (
        <div className="d-flex justify-center pt-5">

        <CircularProgress size={18} color='inherit' />

        </div>
      ) :
      <Form onSubmit={hanldeEditQuestion}>
        <Form.Group className="shadow_def px-3 w-full mb-3">
          <Form.Label className="plusJakara_semibold text_dark">
            Select Country
          </Form.Label>
          <Select
            options={countryOptions}

            onChange={handleChange}
            value={selectCountryOptions}
            placeholder="Select a country"
            on
          />
        </Form.Group>
        {
          findQustion?.exam?.type==='free' ? "" :
        <Form.Group className="shadow_def px-3 w-full mb-3">
            <Form.Label className="plusJakara_semibold text_dark">
              Category
            </Form.Label>
            <Select
              options={categoryOptions}
              value={selectCatOptions}
              onChange={handleCategoryChange}
              placeholder="Select a category"
            />
          </Form.Group>
        }
          <Form.Group className="shadow_def px-3 w-full mb-3">
          <Form.Label className="plusJakara_semibold text_dark">
            Select Exam
          </Form.Label>
          <Select
            options={exams}
            onChange={handleExamChange}
        onMenuScrollToBottom={handlePopupScroll}
        isLoading={searchLoading}
            value={examOptions}
            placeholder="Select exam"
          />
        </Form.Group>
        {/* <Form.Group className="shadow_def mb-4 px-3">
          <Form.Label className="plusJakara_semibold text_dark">
            Select Question
          </Form.Label>
          <div>
            <Select
              onInputChange={(value) => setSearch(value)}
              isLoading={loading}
              value={questionsId}
              // onChange={(value) => handleQuestionsChange(value)}
              onMenuScrollToBottom={handlePopupScroll}
              options={questions?.map((item) => ({
                label: item?.title,
                value: item?._id,
              }))}
            />
          </div>
        </Form.Group> */}
        {findQustion && (
          <div>
            <Form.Group className="shadow_def px-3 mb-3">
              <Form.Label className="plusJakara_semibold text_dark">
                Question Title
              </Form.Label>
              <Form.Control
                type="text"
                required
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                style={{ padding: "10px 20px" }}
                className="custom_control rounded-3 plusJakara_regular text_secondarydark bg_white shadow-sm border"
                placeholder="Enter Name"
              />
            </Form.Group>

            <Form.Group className="shadow_def px-3 mb-3">
              <Form.Label className="plusJakara_semibold text_dark">
                Options
              </Form.Label>
              <div className="d-flex flex-column w-100 gap-3">
                {questions_?.map((val, index) => {
                  return (
                    <div
                      className="d-flex align-items-center w-100 gap-2"
                      key={index}
                    >
                      <input
                        placeholder={`Add option ${index + 1}`}
                        className="bg-white shadow_def p-2 w-100 form-control"
                        type="text"
                        value={val}
                        onChange={(e) => handleQueries(e, index)}
                      />
                      {questions_?.length > 1 && (
                        <div className="">
                          <button
                            type="button"
                            onClick={() => delQuestions(index)}
                            className=" bg-danger text-white d-flex justify-content-center align-items-center rounded-circle"
                            style={{ height: "2rem", width: "2rem" }}
                          >
                            <Trash2 size={20} />
                          </button>
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
              <button
                type="button"
                className="btn btn-sm mt-3 px-3 rounded-4 bg-success text-white"
                onClick={addQuestions}
              >
                Add Options
              </button>
            </Form.Group>
            <Form.Group className="shadow_def px-3 mb-3">
              <Form.Label className="plusJakara_semibold text_dark">
                Select Answer
              </Form.Label>
              <div className="flex w-100 gap-2 items-center">
                <Form.Select
                  value={correctAns}
                  onChange={(e) => setCorrectAns(e.target.value)}
                  className="w-100 rounded-3 text_secondarydark plusJakara_medium bg_white shadow-sm border"
                  style={{ padding: "10px 20px" }}
                >
                  <option className="plusJakara_medium text_dark" value="">
                    Select answer
                  </option>
                  {questions_?.map((item, index) => {
                    return (
                      item && (
                        <option
                          className="plusJakara_medium text_dark"
                          key={index}
                          value={item}
                        >
                          {item}
                        </option>
                      )
                    );
                  })}
                </Form.Select>
              </div>
            </Form.Group>
          </div>
        )}
        <div className="flex justify-content-end my-4 w-100">
          <Button type="submit">
            {editLoader ? (
              <CircularProgress size={18} className="text_white" />
            ) : (
              <span className="plusJakara_semibold text_white text-sm">
                Update
              </span>
            )}
          </Button>
        </div>
      </Form>
    }
    </main>
  );
};

export default EditQuestion;
